<script setup>
import { onMounted, defineProps } from "vue";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import "jspdf-autotable";

const props = defineProps({
  data: {
    type: Array,
  },
  CloseDownload: {
    type: Function,
  },
});

onMounted(async () => {
  //exportTableToPDF();
  exportTableToPDFFormat();
});

// const exportTableToPDF = () => {
//   const table = document.getElementById("exportTable");
//   html2canvas(table).then(() => {
//     const doc = new jsPDF();
//     doc.autoTable({ html: "#exportTable" });
//     // Save or open the PDF
//     doc.save("table_.pdf");
//   });

//   props.CloseDownload();
// };

const exportTableToPDFFormat = () => {
  const table = document.getElementById("exportTable");
  html2canvas(table).then(() => {
    console.log(table);
    const pdf = new jsPDF("p", "pt", "a4");
    pdf.html(table, {
      callback: (pdf) => {
        pdf.save("table.pdf");
      },
    });
  });
  props.CloseDownload();
};
</script>
<template>
  <div class="grid">
    <div class="col-12">
      <h1>User Profile</h1>

      <table id="exportTable">
        <tr>
          <th>First name</th>
          <td>{{ props.data?.FirstName }}</td>
        </tr>
        <tr>
          <th>Last name</th>
          <td>{{ props.data?.LastName }}</td>
        </tr>
        <tr>
          <th>NIC</th>
          <td>{{ props.data?.NIC }}</td>
        </tr>
        <tr>
          <th>Username</th>
          <td>{{ props.data?.Username }}</td>
        </tr>
        <tr>
          <th>Roles</th>
          <td>
            <ul>
              <li :key="Role" v-for="Role in props.data?.Roles">{{ Role }}</li>
            </ul>
          </td>
        </tr>
        <tr>
          <th>NGOs followed</th>
          <td>
            <ul>
              <li :key="NGO" v-for="NGO in props.data?.Ngos">
                {{ NGO.Name }}
              </li>
            </ul>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>
<style>
body {
  font-family: Arial, sans-serif;
  margin: 20px;
}
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}
th,
td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}
th {
  background-color: #f2f2f2;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
</style>
